.header {
  position: relative;
  z-index: 100;
  padding: 30px 0;
  background: #fff transparent;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__menu {
  position: fixed;
  inset: 0 0 0 50%;
  background: #fff;
  padding: 40px 0;
  z-index: 9;
  opacity: 0;
  visibility: hidden;
  transform: translateX(100%);
  transition: all 1s ease;
  border-radius: 20px 0px 0px 20px;
}
.header__menu-dropdown {
  padding: 40px;
  display: flex;
  flex-direction: column;
  gap: 50px;
}
.header__menu-dropdown .header__link {
  font-size: 2rem;
}
.header__button {
  display: flex;
  gap: 30px;
}
@media (max-width: 767.98px) {
  .header__btn {
    display: none;
  }
}

.nav__list {
  display: flex;
  gap: 34px;
}
@media (max-width: 991.98px) {
  .nav__list {
    display: none;
  }
}

.overlay {
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 8;
  opacity: 0;
  visibility: hidden;
  backdrop-filter: blur(5px); /* Làm mờ các phần tử phía sau */
}

.active {
  transform: translateX(0);
  opacity: 1;
  visibility: visible;
}

